<template>
    <div class="row" v-if="ready">
      <div class="col-xs-9 col-md-9">
        <div> <!--general-->
        <form id='general_req_form' enctype="multipart/form-data" class="form-horizontal" data-vv-scope="form1"  @submit.prevent="handleSubmit('form1')" >
        <div class="card">
          <div class="card-header">
            <strong>Reimbursement Form {{ Number(occ_counter)+1 }} out of {{occ_max}} </strong>
          </div>            
          <div class="card-body card-block">
              <div class="row form-group" v-if="level!=2">
                <div class="col col-md-3"><label class="form-control-label">Name of Applicant</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                    <input type="text" class='form-control' v-model='applicant' :readonly='level!=2' required/>
                </div>
            </div>
            <div class="row form-group" v-if="level==2">
                <div class="col col-md-3"><label class="form-control-label">Name of Applicant</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                    <input type="text" class='form-control' v-model='applicant_sec' :readonly='level!=2' required @keyup="search_record_sec"/>
                </div>
            </div>
            <div class="row form-group" v-if="level!=2">
                <div class="col col-md-3"><label class="form-control-label">Procurement ID</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                    <input type="text" class='form-control' v-model='procurement_id' readonly/>
                </div>
            </div>

            <div style="display:flex;flex-direction:row">
                <label style="font-size:0.85em;color:rgba(0,0,0,0.7)">Description of Goods Purchased / Services Delivered:</label>
            </div>
            <div class="row form-group" style="margin-left:-1.26em;margin-right:-1.26em">
            <table class="table table-striped first-td-padding">
                <thead>
                    <tr>
                        <th style='width:15%'></th>
                        <th v-if="counter>=1" style='font-size:1.5em!important'>Item 1
                        <!--<div class="fa fa-plus" style="padding-top:0.4em;padding-left:0.8em;cursor:pointer" @click="new_row(0);cal_sum()"/>
                        <div class="fa fa-minus" style="padding-top:0.4em;padding-left:0.8em;cursor:pointer" @click="remove_row(0);cal_sum()"/>--></th>
                        <th v-for="n in (counter-1)" style='font-size:1.5em!important'>Item {{n+1}}
                        <!--<div class="fa fa-plus" style="padding-top:0.4em;padding-left:0.8em;cursor:pointer" @click="new_row(n);cal_sum()"/>
                        <div class="fa fa-minus" style="padding-top:0.4em;padding-left:0.8em;cursor:pointer" @click="remove_row(n);cal_sum()"/>--></th>                                        
                        </tr>
                </thead>
                <tbody>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Department</td>
                        <td v-if="counter>=1">
                            <div style='display:flex;flex-direction:row'>
                                <div :style="{width:(1/(counter_src[0]+1)*100)+'%'}">
                                    <select v-model="dept[0]" class='form-control select' @change="load_info(0)" required>
                                        <option v-for="dep in dept_list">{{ dep.full_dept }}</option>
                                    </select>
                                </div>
                                <div v-for="s in counter_src[0]" :style="{width:((1/(counter_src[0]+1)*100)-2.5)+'%'}">
                                    <select v-model="dept[s]" class='form-control select' @change="load_info(s)" required style='margin-left:2.5%'>
                                        <option v-for="dep in dept_list">{{ dep.full_dept }}</option>
                                    </select>                                                                     
                                </div>
                            </div>
                        </td>
                    </tr>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Category</td>
                        <td v-if="counter>=1">
                        <div style='display:flex;flex-direction:row'>
                            <div :style="{width:(1/(counter_src[0]+1)*100)+'%'}">
                                <select class="form-control select" v-model="cat[0]" @change="load_bp(0)" required>
                                    <option v-for="cat in cat_list[0]">{{ cat.name }}</option>
                                </select>
                            </div>
                            <div v-for="s in counter_src[0]" :style="{width:((1/(counter_src[0]+1)*100)-2.5)+'%'}">
                                <select class="form-control select" v-model="cat[s]" @change="load_bp(s)" required style='margin-left:2.5%'>
                                    <option v-for="cat in cat_list[s]">{{ cat.name }}</option>
                                </select>                                                                     
                            </div>
                        </div>    
                        </td>
                    </tr>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Nature</td>
                        <td v-if="counter>=1">
                            <textarea class="form-control" maxlength="100" rows="2" v-model="nature[0]" required/>
                        </td>
                        <td v-for="n in (counter-1)">
                            <textarea class="form-control" maxlength="100" rows="2" v-model="nature[n]" required/>
                        </td>
                    </tr>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Location</td>
                        <td v-if="counter>=1">
                            <input class="form-control" v-model="loc[0]" required readonly/>
                        </td>
                        <td v-for="n in (counter-1)">
                            <input class="form-control" v-model="loc[n]" required readonly/>
                        </td>
                    </tr>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Reason for application</td>
                        <td v-if="counter>=1">
                            <textarea class="form-control" rows="2" maxlength="100" v-model="reason[0]" required/>
                        </td>
                        <td v-for="n in (counter-1)">
                            <textarea class="form-control" rows="2" maxlength="100" v-model="reason[n]" required/>
                        </td>
                    </tr>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Budget Plan Category</td>
                        <td v-if="counter>=1">
                        <div style='display:flex;flex-direction:row'>
                            <div :style="{width:(1/(counter_src[0]+1)*100)+'%'}">
                                <select class="form-control select" v-model="bp[0]" required @change="new_bp(0)">
                                    <option v-for="bpl in bp_list[0]">{{ bpl.description }}</option>
                                </select>
                            </div>
                            <div v-for="s in counter_src[0]" :style="{width:((1/(counter_src[0]+1)*100)-2.5)+'%'}">
                                <select class="form-control select" v-model="bp[s]" required @change="new_bp(s)" style='margin-left:2.5%'>
                                    <option v-for="bpl in bp_list[s]">{{ bpl.description }}</option>
                                </select>                                                                     
                            </div>
                        </div>
                        </td>
                    </tr>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Fund Source</td>
                        <td v-if="counter>=1">
                        <div style='display:flex;flex-direction:row'>
                            <div :style="{width:(1/(counter_src[0]+1)*100)+'%'}">
                                <select class="form-control select" v-model="fund[0]" required>
                                    <option v-for="f in fund_list[0]">{{ f.fundsrc }}</option>
                                </select>
                            </div>
                            <div v-for="s in counter_src[0]" :style="{width:((1/(counter_src[0]+1)*100)-2.5)+'%'}">
                                <select class="form-control select" v-model="fund[s]" required style='margin-left:2.5%'>
                                    <option v-for="f in fund_list[s]">{{ f.fundsrc }}</option>
                                </select>                                                                     
                            </div>
                        </div>
                        </td>
                    </tr>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Amount HK$</td>
                        <td v-if="counter>=1">
                        <div style='display:flex;flex-direction:row'>
                            <div :style="{width:(1/(counter_src[0]+1)*100)+'%'}">
                                <input type="number" step=0.01 class="form-control" v-model='amount[0]' :max='fund_max[0]' required @change="cal_sum(0)" value=0 />
                            </div>
                            <div v-for="s in counter_src[0]" :style="{width:((1/(counter_src[0]+1)*100)-2.5)+'%'}">
                                <input type="number" step=0.01 class="form-control" v-model='amount[s]' :max='fund_max[0]' required @change="cal_sum(0)" value=0 style='margin-left:2.5%'/>
                            </div>
                        </div>
                        </td>
                    </tr>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Support document (.pdf)</td>
                        <td colspan='100%'> 
                            <input type="file" class="form-control-button" accept="application/pdf" ref="pdf_file" multiple @change="addfile_pdf()" style="height:100%" required/>
                        </td>
                    </tr>
                    <tr>
                        <td style='width:15%;font-size:1.75em!important;padding-left:1.5em'>Invoice Number</td>
                        <td colspan='100%'> 
                            <input type="text" class="form-control" v-model="invoice" placeholder="Separate multiple invoice with ," required/>
                        </td>
                    </tr>
                </tbody>
            </table>            
            </div>
            <div class="row form-group">
                <div class="col col-md-12">
                    <div style="display:flex;flex-direction:row">
                        <div style="width:15%;padding-left:0.3%">Recipient type :</div>
                        <input type="radio" name="type_rec" v-model="rtype" value="staff" style="margin-left:2em;margin-top:0.2em" @click="clr" required><label>Staff</label>
                        <input type="radio" name="type_rec" v-model="rtype" value="company" style="margin-left:1em;margin-top:0.2em" @click="clr"><label>Company</label>
                    </div>
                    <div style="display:flex;flex-direction:row;padding-left:0" class='col-6'>
                        <label style="padding-top:0.4em;;padding-left:0.55%;padding-right:0;width:30.45%;">Name of recipient :</label>
                        <input type="text" v-model="search_rec" class="form-control input col-8"  @keyup="search_record" required/>
                    </div>
                    <div v-if="new_sup==true" style='padding-top:0.4em;'>
                        <div style='display:flex;flex-direction:row;width:100%;padding-left:0' class='col-6'>
                            <div class='col-8' style='padding-left:0'>
                                <input type="text" class="form-control" placeholder="Enter Company Address" v-model="company_address" required/>
                            </div>
                            <div class='col-4' style='padding-left:0;padding-right:0'>
                                <input type="text" class="form-control" placeholder="Enter Company Contact" v-model="company_contact" required/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
             <div class="row form-group">
                <div class="col col-md-12">
                    <div style="display:flex;flex-direction:row">
                    <div v-if="signed==false" class="col-6" style="padding-left:0">
                        <div style="display:flex;flex-direction:row;padding-left:0">
                            <label style="width:30.45%;padding-top:0.4em;;padding-left:0.55%;padding-right:0">    
                                Security Code : 
                            </label>
                                <input type="password" v-model='pwd' class="form-control col-5"/>                   
                                <button type="button" class="form-control col-3" style='padding-left:0' ref="submitcode" @click="post_ssc">Sign now !</button>         
                        </div>   
                    </div>
                    <div v-if="signed" style="display:flex;flex-direction:row;">
                        <label class="form-control-label" style="margin-right:1em;padding-top:0.4em;margin-top:2.5em">    
                            Signature 
                        </label>
                            <img :src="sign_data[0].signature" style="width:auto;height:5em;border-bottom:1px solid black;margin-left:0.5em"/>        
                    </div>        
                    <label class="form-control-label" style="margin-left:auto;margin-top:2.5em">
                        <b>Total Amount in HK$: {{ sum }}</b>
                    </label>
                    </div>
                </div>
            </div>
            <div v-html="message">{{message}}</div>
            </div>     

        <div class="card-footer">
            <button type="submit" class="btn btn-primary btn-sm" :disabled="processing">
              <i class="fa fa-paper-plane"></i> Submit
            </button>
            <button type="reset" class="btn btn-danger btn-sm" onclick="document.getElementById('general_req_form').reset()">
              <i class="fa fa-ban"></i> Reset
            </button>
        </div>
      </form>
    </div>  
      <!--check form-->  
    </div> 
        <div class="col-xs-3 col-md-3" style='position:fixed;right:250px;top:250px;box-shadow: #888888;'>
           <v-data-table
                :headers="headers"
                :items="recep_data"
                :search="search"
                class="elevation-1"
                :rows-per-page-items="[10]"
                v-if="hv_record_sec"
                >
            <template v-slot:items="props">
                <td><a href="" :id= props.item.username v-on:click.prevent="select_user_sec(props.item.name)">Select</a></td>
                <td>{{ props.item.name }}</td>
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>
            
            </template>
            </v-data-table>  
        </div> 
        <div class="col-xs-3 col-md-3" style='position:fixed;right:250px;top:250px;box-shadow: #888888;'>
           <v-data-table
                :headers="headers"
                :items="recep_data"
                :search="search"
                class="elevation-1"
                :rows-per-page-items="[10]"
                v-if="hv_record"
                >
            <template v-slot:items="props">
                <td><a href="" :id= props.item.username v-on:click.prevent="select_user(props.item.name)">Select</a></td>
                <td>{{ props.item.name }}</td>
            <template v-slot:pageText="props">
                Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
            </template>
            
            </template>
            </v-data-table>  
        </div> 
    </div>
</template>


<script>
import axios from 'axios';
export default {
    data(){          
        var tmr =document.getElementById('tmr_date').value;
        return{
            processing:false,
            invoice:'',
            data_list:[],
            counter_list:[],
            occ_counter:0,
            occ_max:0,
            sum_max:0,
            tmp_cat_id:'',
            counter_src:[],
            reimb_type:'Recursive',
            dept:[],
            procurement_id : '',
            dept_list:[],
            sub_dept_list:[],
            asset_list:[],
            fund_list:[],
            check_num:true,
            //table
            sum:0,
            cat_list:[],
            cat:[],
            nature:[],
            loc:[],
            reason:[],
            bp:[],
            bp_tag:[],
            fund:[],
            amount:[0,0,0],
            pwd:'',
            ready:false,
            new_sup:false,
            company_address:'',
            company_contact:'',
            //table
            //sign
            sign_data:[],
            fund_cost:[],         
            loc_list:[],
            bp_list:[],
            recep_data:[],
            fund_max:[],
            fund_max_ini:[],
            counter:1,
            applicant_sec:'',
            hv_record_sec:false,
            pdf:'',
            level:0,
            message:'',
            user_check:'',
            signed:false,
            rtype:'company',
            search_rec:'',
            hv_record:false,
            proc_data:[],
            headers: [
            { text: 'Action',value :'action',sortable: false},
            { text: 'Name',value:'name',sortable: false},
            ], 
        }  
    },
async created(){
    await axios.get('../permission.php')
    .then(
        (response)=>{
            this.level = response.data[0].proc_level;
        }
    )
    await axios.get('Procurement/shared_backend/check_user_setup.php')
        .then(
            (response)=>{
                this.user_check = response.data[0].signed;
                var perm = response.data[0];
                if(!(this.user_check=='set')){
                    this.$router.push('../user_setup');
                }
                if(perm.proc_level<1){
                    this.$alert("No permission").then(
                        ()=>{
                            this.$router.go(-1);
                        }
                    )
                }
            }
        )
    await axios.get('Procurement/department/backend/dept_member_reimb.php')
        .then(
            (response)=>{
                this.dept_list = response.data;
                var d = 0;
                for(d=0;d<this.counter+1;d++){
                    this.dept[d] = this.dept_list[0].full_dept;
                }
                
            }
        ); 
    await axios.get('../user_info.php')
        .then(
            (response)=>{
                this.applicant = response.data[1];
            }
        ); 
    await axios.get('Procurement/shared_backend/location.php')
        .then(
            (response)=>{
                this.loc_list = response.data;

            }
        );     
    
    var formData = new FormData();
    formData.append('dept',this.dept[0]);
    await axios.post('Procurement/budget/backend/asset.php',formData)
            .then(
                (response)=>{
                var i;
                for(i=0;i<this.counter+1;i++){
                    this.cat_list[i] = response.data;                  
                    this.cat[i] = this.cat_list[i][0].name;
                }
                }
            )
    formData.append('cat',this.cat[0]);
    await axios.post('Procurement/shared_backend/fetch_bp_item.php',formData)
            .then(
                (response)=>{
                    var i = 0;
                    for(i=0;i<this.counter+1;i++){
                        this.bp_list[i] = response.data;
                        this.bp[i] = this.bp_list[i][0].description;
                        this.bp_tag[i] = this.bp_list[i][0].tag;
                        this.fund[i] = this.bp_list[i][0].fundsrc;  
                    }
                }
            )
    

    //fetch from procurement (dept,category,item(nature) company name)
    var formData_fetch_proc = new FormData();
    var proc_id = document.getElementById('tmp_id').value;
    this.procurement_id = proc_id;
    formData_fetch_proc.append('proc_id',proc_id);
    await axios.post('Procurement/reimbursement/backend/get_counter.php',formData_fetch_proc)
        .then(
            (response)=>{
                this.counter_list = response.data;
                this.occ_counter = this.counter_list[0].counter;
            }
        )
    await axios.post('Procurement/reimbursement/backend/fetch_from_proc.php',formData_fetch_proc)
        .then(
            (response)=>{
                this.counter_list = response.data;
                this.occ_max = this.counter_list[0].occur;
            }
        )
    await axios.post('Procurement/reimbursement/backend/get_recur_reimb.php',formData_fetch_proc)
        .then(
            (response)=>{
                this.counter_list = response.data;
                this.counter_src[0] = this.counter_list.length-1;
                var i = 0;
                for(i=0;i<(this.counter_src[0]+1);i++){
                    this.amount[i] = this.counter_list[i].amount;
                    this.dept[i] = this.counter_list[i].dept_full;
                    this.cat[i] = this.counter_list[i].asset_full;
                    this.bp[i] = this.counter_list[i].bp_d;
                    this.fund[i] = this.counter_list[i].fundsrc;
                    this.nature[i] = this.counter_list[i].nature;
                    this.loc[i] = this.counter_list[i].location;
                    this.reason[i] = this.counter_list[i].reason;    
                    this.rtype = this.counter_list[i].receiver_type;
                    this.search_rec = this.counter_list[i].receiver_name;

                }                
            }
        )
         var i = 0;
        for(i=0;i<(this.counter_src[0]+1);i++){
            var formData = new FormData();       
            formData.append('dept',this.dept[i]);
            await axios.post('Procurement/budget/backend/asset.php',formData)
                    .then(
                        (response)=>{
                            this.cat_list[i] = response.data;
                            this.$forceUpdate();
                    }
                    )
            await formData.append('cat',this.cat[i]);
            await axios.post('Procurement/shared_backend/fetch_bp_item.php',formData)
                .then(
                    (response)=>{
                            this.bp_list[i] = response.data; 
                            this.$forceUpdate();
                    }
                )
            await formData.append('bp',this.bp[i]);   
            await axios.post('Procurement/shared_backend/fetch_bp_fund.php',formData)
                .then(
                    (response)=>{
                            this.fund_list[i] = response.data;
                            this.$forceUpdate();
                    }
                )
        }
        this.counter = 1;
        this.sum_max = 9999999;
        this.ready = true; 
        
    

    //fetch from procurement (dept,category,item(nature) company name)
    
         
    //asset category of department   
  },
 async mounted(){

},   
    methods:{       
        async load_info(event){   //change dept update all
        var formData = new FormData();
        formData.append('dept',this.dept[event]);
        await axios.post('Procurement/budget/backend/asset.php',formData)
                .then(
                    (response)=>{
                        this.cat_list[event] = response.data;
                        this.cat[event] = this.cat_list[event][0].name;
                        this.$forceUpdate();
                }
                )
        await formData.append('cat',this.cat[event]);
        await axios.post('Procurement/shared_backend/fetch_bp_item.php',formData)
            .then(
                (response)=>{
                        this.bp_list[event] = response.data; 
                        this.bp[event] = this.bp_list[event][0].description;
                        this.bp_tag[event] = this.bp_list[event][0].tag;   
                        this.$forceUpdate();
                }
            )
        await formData.append('bp',this.bp[event]);   
        await axios.post('Procurement/shared_backend/fetch_bp_fund.php',formData)
            .then(
                (response)=>{
                        this.fund_list[event] = response.data;
                        this.fund[event] = this.fund_list[event][0].fundsrc;
                        this.fund_max[event] = parseFloat(this.fund_list[event][0].cost_total) - parseFloat(this.fund_list[event][0].expense);
                        this.fund_max_ini[event]=this.fund_max[event];
                        this.$forceUpdate();
                }
            )
        this.$forceUpdate();
        },
        
       
        async load_info_id(id){
            var sel = document.getElementById(id);
            var dept= sel.options[sel.selectedIndex].text;
            var formData = new FormData();
            formData.append('dept',dept);
            await axios.post('Procurement/budget/backend/asset.php',formData)
                    .then(
                        (response)=>{
                            this.cat_list[id] = response.data;
                            this.tmp_cat_id = 'cat_'+id.substring(5);
                            this.$forceUpdate();
                    }
                    )
            var cat_id = this.tmp_cat_id;
            var sel = document.getElementById(cat_id);
            var cat = sel.options[sel.selectedIndex].text;
            await formData.append('cat',cat);
            await axios.post('Procurement/shared_backend/fetch_bp_item.php',formData)
                .then(
                    (response)=>{
                            this.bp_list[cat_id] = response.data; 
                            this.tmp_cat_id = 'bp_'+id.substring(5);
                            this.$forceUpdate();
                    }
                )
            var bp_id = this.tmp_cat_id;
            var sel = document.getElementById(bp_id);
            var bp = sel.options[sel.selectedIndex].text;   
            await formData.append('bp',bp);   
            await axios.post('Procurement/shared_backend/fetch_bp_fund.php',formData)
                .then(
                    (response)=>{
                            this.fund_list[bp_id] = response.data;
                            this.$forceUpdate();
                    }
                )
            this.$forceUpdate();
        },
        load_bp_id(id){
            var tmp_id = id.substring(4);
            var dept_id = 'dept_'+tmp_id;
            var dept_sel = document.getElementById(dept_id);
            var dept = dept_sel.options[dept_sel.selectedIndex].text;
            var cat_id = 'cat_'+tmp_id;
            var cat_sel = document.getElementById(cat_id);
            var cat = cat_sel.options[cat_sel.selectedIndex].text;
            var bp_id = 'bp_'+tmp_id;
            this.tmp_cat_id = bp_id;
            var target = '';
            var formData = new FormData();     
            formData.append('dept',dept);
            formData.append('cat',cat);
            axios.post('Procurement/shared_backend/fetch_bp_item.php',formData)
            .then(
                (response)=>{
                    if(!(response.data=='')){
                        this.bp_list[cat_id] = response.data; 
                        var f_id = this.tmp_cat_id;
                        this.fund_list[f_id] = response.data;                    
                        this.$forceUpdate();
                    } else {
                        var f_id = this.tmp_cat_id;
                        this.bp_list[cat_id] = ['']; 
                        this.fund_list[f_id] = [''];
                        this.$forceUpdate();
                    }
                }
            )       
        },
        async load_bp(event){
            var target = '';
            var formData = new FormData();        
            formData.append('dept',this.dept[event]);
            formData.append('cat',this.cat[event]);
            await axios.post('Procurement/shared_backend/fetch_bp_item.php',formData)
            .then(
                (response)=>{
                       this.bp_list[event] = response.data;
                       this.fund_list[event] = response.data;
                       this.bp[event] = this.bp_list[event][0].description;
                       this.bp_tag[event] = this.bp_list[event][0].tag;
                       this.fund[event] = this.fund_list[event][0].fundsrc;
                       this.fund_max[event] = parseFloat(this.bp_list[event][0].cost_total) - parseFloat(this.bp_list[event][0].expense);
                       this.fund_max_ini[event]=this.fund_max[event];
                       this.$forceUpdate();
                }
            )        
        },
        post_ssc(event){
            var formData = new FormData();
            formData.append('pwd',this.pwd);
            axios.post('Procurement/reimbursement/backend/check_sign.php',formData)
            .then(
                (response)=>{
                    this.sign_data = response.data;
                    if(response.data[0].signature==undefined){
                        this.message = response.data;
                    } else {
                    this.signed=true;
                    this.message='';
                    }
                }
            )
        this.$forceUpdate();
        },
        
        cal_num(event){
            this.total = this.unit_cost * this.quantity;
            if(isNaN(this.total)){
                this.total = '...';
                this.check_num = false;
            }
            this.fund_cost[0] = this.total;
            this.$forceUpdate();
        },
        new_row(event){
            this.counter_src[event] = parseInt(this.counter_src[event])+1;
            var n = this.counter_src[event];
            this.cat_list[('dept_'+event+'_'+n)] = this.cat_list[event];
            this.bp_list[('cat_'+event+'_'+n)] = this.bp_list[event];
            this.fund_list[('bp_'+event+'_'+n)] = this.fund_list[event];
            this.$forceUpdate();
        },
        remove_row(event){
            if(!(this.counter_src[event]==0)){
            this.counter_src[event] = parseInt(this.counter_src[event])-1;
            }
            this.$forceUpdate();
        },
        select_user(event){
            this.search_rec = event;
            this.hv_record = false;
            this.new_sup = false;
        },
        select_user_sec(event){
            this.applicant_sec = event;
            this.hv_record_sec = false;
        },
         search_record_sec(event){
            var formData = new FormData();
            formData.append('key', this.applicant_sec);
            axios.post('RequestPortal/backend/search_user_list.php',formData)
            .then(
                (response)=>{
                    if(response.data == "Empty"){
                       this.hv_record_sec=false; 
                    } else {
                        this.recep_data = response.data;
                        this.hv_record_sec=true;
                    }
                }); 
        },
        search_record(event){
            var formData = new FormData();
            formData.append('key', this.search_rec);
            if(this.rtype=='staff'){
            axios.post('RequestPortal/backend/search_user_list.php',formData)
            .then(
                (response)=>{
                    if(response.data == "Empty"){
                       this.hv_record=false; 
                    } else {
                        this.recep_data = response.data;
                        this.recep_data.username = response.data.username;
                        this.recep_data.name = response.data.name;
                        this.hv_record=true;
                    }
                }); 
            } else if(this.rtype=='company'){
            axios.post('Procurement/shared_backend/supplier_list.php',formData)
            .then(
                (response)=>{
                    if(response.data == "Empty"){
                       this.hv_record=false; 
                       this.new_sup=false;
                    } else if(response.data == "new"){
                        this.new_sup = true;
                    }
                    else {                       
                        this.recep_data = response.data;
                        this.recep_data.username = response.data.username;
                        this.recep_data.name = response.data.name;
                        this.hv_record=true;
                        this.new_sup=false;
                    }
                }); 
            } 
        },
        clr(event){
            this.search_rec='';
            this.hv_record=false;
        },
        addfile_pdf(event){
             this.pdf = this.$refs.pdf_file.files;
        },
        cal_sum(event){
            if(this.amount[event]==''){
                this.amount[event] = 0;
            };
            // 0 case
            if(event==0){
                if((this.cat[0]==this.cat[1])&&(this.bp[0]==this.bp[1])&&(this.fund[0]==this.fund[1])){
                    this.fund_max[1] = parseFloat(this.fund_max_ini[0]);
                    this.fund_max[1] = parseFloat(this.fund_max[1])-parseFloat(this.amount[0])-parseFloat(this.amount[2]);
                }
                if((this.cat[0]==this.cat[2])&&(this.bp[0]==this.bp[2])&&(this.fund[0]==this.fund[2])){
                    this.fund_max[2] = parseFloat(this.fund_max_ini[0]);
                    this.fund_max[2] = parseFloat(this.fund_max[2])-parseFloat(this.amount[0])-parseFloat(this.amount[1]);
                }
            }
            else if(event==1){
                if((this.cat[1]==this.cat[0])&&(this.bp[1]==this.bp[0])&&(this.fund[1]==this.fund[0])){
                    this.fund_max[0] = parseFloat(this.fund_max_ini[1]);
                    this.fund_max[0] = parseFloat(this.fund_max[0])-parseFloat(this.amount[1])-parseFloat(this.amount[2]);
                }
                if((this.cat[1]==this.cat[2])&&(this.bp[1]==this.bp[2])&&(this.fund[1]==this.fund[2])){
                    this.fund_max[2] = parseFloat(this.fund_max_ini[1]);
                    this.fund_max[2] = parseFloat(this.fund_max[2])-parseFloat(this.amount[1])-parseFloat(this.amount[0]);
                }
            } else if(event==2){
                if((this.cat[2]==this.cat[0])&&(this.bp[2]==this.bp[0])&&(this.fund[2]==this.fund[0])){
                    this.fund_max[0] = parseFloat(this.fund_max_ini[2]);
                    this.fund_max[0] = parseFloat(this.fund_max[0])-parseFloat(this.amount[2])-parseFloat(this.amount[1]);
                }
                if((this.cat[2]==this.cat[1])&&(this.bp[2]==this.bp[1])&&(this.fund[2]==this.fund[1])){
                    this.fund_max[1] = parseFloat(this.fund_max_ini[2]);
                    this.fund_max[1] = parseFloat(this.fund_max[1])-parseFloat(this.amount[2])-parseFloat(this.amount[0]);
                }
            }

            //update max for same src
            var i = 0;
            this.sum = 0;
            for(i=0;i<this.counter;i++){
            this.sum = Number(this.sum) + Number(this.amount[i]);
            };
        },
        new_bp_id(id){
            var tmp_id = id.substring(3);

            var dept_id = 'dept_'+tmp_id;
            var dept_sel = document.getElementById(dept_id);
            var dept = dept_sel.options[dept_sel.selectedIndex].text;
            var cat_id = 'cat_'+tmp_id;
            var cat_sel = document.getElementById(cat_id);
            var cat = cat_sel.options[cat_sel.selectedIndex].text;
            var bp_id = 'bp_'+tmp_id;
            var bp_sel = document.getElementById(bp_id);
            var bp = bp_sel.options[bp_sel.selectedIndex].text;

            var formData = new FormData();
            formData.append('dept',dept);
            formData.append('cat',cat);
            formData.append('bp',bp);
            axios.post('Procurement/shared_backend/fetch_bp_fund.php',formData,{
            headers: {
                'Content-Type': 'multipart/form-data'
            }
          })
                .then(
                    (response)=>{                                               
                            this.fund_list[bp_id] = response.data;                                            
                    }
                )
        this.$forceUpdate();
        },
        new_bp(event){
            var formData = new FormData();
            formData.append('dept',this.dept[event]);
            formData.append('cat',this.cat[event]);
            formData.append('bp',this.bp[event]);
            axios.post('Procurement/shared_backend/fetch_bp_fund.php',formData,{
            headers: {
                'Content-Type': 'multipart/form-data'
            }
          })
                .then(
                    (response)=>{                                               
                            this.fund_list[event] = response.data;                       
                            this.fund[event] = this.fund_list[event][0].fundsrc;
                            this.fund_max[event] = parseFloat(this.fund_list[event][0].cost_total) - parseFloat(this.fund_list[event][0].expense);
                            this.fund_max_ini[event]=this.fund_max[event];                       
                    }
                )
        this.$forceUpdate();
        },
        async handleSubmit(event){
            if(!(this.sum>this.sum_max)){
                if((this.level!=2)&&this.signed){
                var formData = new FormData();
                var i=0;
                var count_files = 0;
                formData.append('invoice',this.invoice);
                formData.append('occur',this.occ_counter);
                formData.append('proc_id',this.procurement_id);
                formData.append('applicant',this.applicant);               
                formData.append('newsup',this.new_sup);
                if(this.new_sup==true){
                    formData.append('address',this.company_address); 
                    formData.append('contact',this.company_contact); 
                }
                //counter loop
                for(i=0;i<this.counter_src[0]+1;i++){
                    formData.append('dept'+i,this.dept[i]);
                    formData.append('asset_cat'+i,this.cat[i]);
                    formData.append('nature'+i,this.nature[i]);
                    formData.append('location'+i,this.loc[i]);
                    formData.append('reason'+i,this.reason[i]);
                    formData.append('bp'+i,this.bp[i]);
                    formData.append('fundsrc'+i,this.fund[i]);
                    formData.append('amount'+i,this.amount[i]);
                    formData.append('counter_src_'+i,this.counter_src[i]);
                }
                for( var i = 0; i < this.pdf.length; i++ ){
                let file = this.pdf[i];
                formData.append('files'+i, file);
                count_files=count_files+1;
                }
                formData.append('count_files', count_files);
                //counter loop
                formData.append('total',this.sum);
                formData.append('reimb_type',this.reimb_type);
                formData.append('r_type',this.rtype);
                formData.append('r_name',this.search_rec);
                formData.append('sign_stat',"true");     
                formData.append('counter',this.counter);
                this.processing = true;
                await axios.post('Procurement/reimbursement/backend/recur_reimb_child.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='OK'){
                            this.$alert('Successfully submitted record').then(
                                ()=>{
                                    this.processing = false;
                                }
                            )  
                            this.$router.push('../procurement/view_proc');                         
                        } else {
                            this.$alert(response.data);
                        } 
                    }
                )
                } else if((this.level==2)&&this.signed==false){
                var formData = new FormData();
                var i=0;
                var count_files = 0;
                formData.append('occur',this.occ_counter);
                formData.append('proc_id',this.procurement_id);
                formData.append('applicant',this.applicant_sec);            
                formData.append('newsup',this.new_sup);
                if(this.new_sup==true){
                    formData.append('address',this.company_address); 
                    formData.append('contact',this.company_contact); 
                }
                //counter loop
                for(i=0;i<this.counter_src[0]+1;i++){
                    formData.append('dept'+i,this.dept[i]);
                    formData.append('asset_cat'+i,this.cat[i]);
                    formData.append('nature'+i,this.nature[i]);
                    formData.append('location'+i,this.loc[i]);
                    formData.append('reason'+i,this.reason[i]);
                    formData.append('bp'+i,this.bp[i]);
                    formData.append('fundsrc'+i,this.fund[i]);
                    formData.append('amount'+i,this.amount[i]);
                    formData.append('counter_src_'+i,this.counter_src[i]);
                }
                for( var i = 0; i < this.pdf.length; i++ ){
                let file = this.pdf[i];
                formData.append('files'+i, file);
                count_files=count_files+1;
                }
                formData.append('count_files', count_files);
                //counter loop
                formData.append('total',this.sum);
                formData.append('reimb_type',this.reimb_type);
                formData.append('r_type',this.rtype);
                formData.append('r_name',this.search_rec);
                formData.append('sign_stat',"true");     
                formData.append('counter',this.counter);
                this.processing = true;
                await axios.post('Procurement/reimbursement/backend/recur_reimb_child.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='OK'){
                            this.$alert('Successfully submitted record').then(
                                ()=>{
                                    this.processing = false;                             
                                }
                            )    
                            this.$router.push('../procurement/view_proc');                        
                        } else {
                            this.$alert(response.data);
                        } 
                    }
                )
                } else if((this.level!=2)&&(this.signed==false)){
                    this.$alert('Please Sign the form !');
                }
            } else {
                this.$alert('Total Amount Should not exist HK$'+this.sum_max);
            }
        }
        },
        
}
</script>


<style scoped>
  .input-group-addon {
    background-color: transparent;
    border-left: 0;
}
input[type=date]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    display: none;
}
</style>


